<template>
  <div class="modal">
    <div class="modal__back" @click="close"></div>
    <div class="modal__content">
      <span @click="close" class="modal__close">&times;</span>
      <h3>{{ title }}</h3>
      <p>Mensaje actual: {{ mensaje }}</p>
      <input
        class="slider-input"
        type="text"
        :name="mensaje"
        v-model="newMensaje"
        :placeholder="mensaje"
        autocomplete="off"
      />
      <button
        v-on:click="hacemeElPost(newMensaje, title)"
        class="slider-button"
      >
        Cambiar
      </button>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "cambiarMensaje",
  props: {
    title: String,
    mensaje: String
  },
  methods: {
    close() {
      this.$emit("close");
    },
    hacemeElPost: function(newMensaje, title) {
   //   const url = "http://localhost:4000/mensajes/0";
      const url = "https://my-json-server.typicode.com/nachdiaz/api-json/mensajes/0";

      axios
        .patch(url, {
          [title]: newMensaje
        })
        .then(resp => {
          console.log(resp.data);
          window.location.reload();
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
