<template>
    <main>
        <h1>Mensajes precargados</h1>
        <section style="display: grid;grid-template-columns: repeat(auto-fit, minmax(18rem,1fr));grid-gap: 1rem;">
            <div v-for="(item, index) in Object.entries(mensajes)" :key="index" class="card">
                <div v-if="typeof item[1] != 'object'" class="card-content">
                    <h2>{{ item[0] }}</h2>
                    <p class="card-body">
                        {{ item[1] }}
                    </p>
                    <footer class="edit-btn"><button @click="showModal(item[0], item[1])" type="button">Editar</button></footer>
                </div>
                <div v-else-if="item[1] != null" class="card-content">
                    <h2>{{ item[0] }}</h2>
                    <div v-for="(innerItem, innerIndex) in Object.entries(item[1])" :key="innerIndex+10" class="card-body">
                        <div v-if="!Array.isArray(innerItem[1])">
                            <strong>{{ innerItem[0] }}:</strong>
                            <p>
                                {{ innerItem[1] }}
                            </p>
                            <p class="edit-btn"><button @click="showModalOne(item[0],innerItem[1], innerItem[0])" type="button">Editar</button></p>

                        </div>
                        <div v-else>
                            <strong>{{ innerItem[0] }}:</strong>
                            <div v-for="(it2, in2) in innerItem[1]" :key="in2">
                                {{ it2 }}
                                <p class="edit-btn"><button @click="showModalTwo(item[0],it2,innerItem[0] )" type="button">Editar</button></p>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-else class="card-content">
                    <h2>{{ item[0] }}</h2>
                    <p style="color:#AAAAAA;" class="card-body">
                        Mensaje no cargado
                    </p>
                    <footer class="edit-btn"><button @click="showModal(item[0], item[1])" type="button">Editar</button></footer>
                </div>
            </div>
        </section>
        <cambiarMensaje v-show="isModalVisible" @close="closeModal" :title="title" :mensaje="mensaje" />
        <cambiarMensajeOne v-show="isModalOneVisible" @close="closeModal" :title="title" :duenio="duenio" :mensaje="mensaje" />
        <cambiarMensajeTwo v-show="isModalTwoVisible" @close="closeModal" :title="title" :duenio="duenio" :mensaje="mensaje" />
    </main>
</template>
<script>
    import axios from "axios";
    import cambiarMensaje from "./cambiarMensaje.vue";
    import cambiarMensajeOne from "./cambiarMensajeOne.vue";
    import cambiarMensajeTwo from "./cambiarMensajeTwo.vue";

    export default {
        name: "mensajes",
        props: {},
        components: {
            cambiarMensaje,
            cambiarMensajeOne,
            cambiarMensajeTwo
        },
        data() {
            return {
                mensajes: [],
                newMensaje: '',
                isModalVisible: false,
                isModalOneVisible: false,
                isModalTwoVisible: false
            };
        },
        mounted() {
        //    const url = "http://localhost:3000/mensajes/";
              const url = "https://my-json-server.typicode.com/nachdiaz/api-json/mensajes/";
            axios.get(url).then(response => {
                this.mensajes = response.data[0];
            });
        },
        methods: {
            showModal(title, mensaje) {
                this.isModalVisible = true;
                this.title = title;
                this.mensaje = mensaje;
            },
            showModalOne(title, mensaje, duenio) {
                this.isModalOneVisible = true;
                this.title = title;
                this.duenio = duenio;
                this.mensaje = mensaje;
            },
            showModalTwo(title, mensaje, duenio) {
                this.isModalTwoVisible = true;
                this.title = title;
                this.duenio = duenio;
                this.mensaje = mensaje;
            },
            closeModal() {
                this.isModalVisible = false;
                this.isModalOneVisible = false;
                this.isModalTwoVisible = false;

            }
        }
    };

</script>
