<template>
    <main>
        <h1>Configuración</h1>
        <section style="display: grid;grid-template-columns: repeat(auto-fit, minmax(18rem,1fr));grid-gap: 1rem;">
            <div v-for="(item, index) in Object.entries(configuracion)" :key="index" class="card">
                <div class="card-content">
                    <h2>{{ item[0] }}</h2>
                    <p class="card-body">
                        {{ item[1] }}
                    </p>
                    <footer class="edit-btn"><button @click="showModal(item[0], item[1])" type="button">Editar</button></footer>
                </div>
            </div>
        </section>
        <cambiarConfig v-show="isModalVisible" @close="closeModal" :title="title" :mensaje="mensaje" />
    </main>
</template>
<script>
    import axios from "axios";
    import cambiarConfig from "./cambiarConfig.vue";

    export default {
        name: "configuracion",
        props: {},
        components: {
            cambiarConfig,
        },
        data() {
            return {
                configuracion: [],
                newMensaje: '',
                isModalVisible: false,
            };
        },
        mounted() {
   //         const url = "http://localhost:3000/configuracion/";
            const url = "https://my-json-server.typicode.com/nachdiaz/configjson/configuracion/";
            axios.get(url).then(response => {
                this.configuracion = response.data[0];
            });
        },
        methods: {
            showModal(title, mensaje) {
                this.isModalVisible = true;
                this.title = title;
                this.mensaje = mensaje;
            },
            closeModal() {
                this.isModalVisible = false;
                this.isModalOneVisible = false;
                
            }
        }
    };

</script>
